  function formatDate(type) {
    const datetime = new Date()
    if (type === 1) {
      // 23/01/2024
      return datetime.toLocaleDateString("id-ID", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric"
      })
    } else if (type === 2) {
      // 23/01/2024 21:25
      const option2 = `${formatDate(1)} ${datetime.toLocaleTimeString("id-ID", {
        hour: "2-digit",
        minute: "2-digit"
      })}`
      return option2
    } else if (type === 3) {
      // 23 Januari 2024
      return datetime.toLocaleDateString("id-ID", {
        day: "2-digit",
        month: "long",
        year: "numeric"
      })
    } else if (type === 4) {
      // 23 Januari 2024 Jam 21:25
      const option4 = `${formatDate(3)} Jam ${datetime.toLocaleTimeString(
        "id-ID",
        { hour: "2-digit", minute: "2-digit" }
      )}`
      return option4
    } else if (type === 5) {
      // 23 Jan 2024
      return datetime.toLocaleDateString("id-ID", {
        day: "2-digit",
        month: "short",
        year: "numeric"
      })
    } else if (type === 6) {
      // 23-01-2024
      return datetime
        .toLocaleDateString("en-GB", {
          day: "2-digit",
          month: "2-digit",
          year: "numeric"
        })
        .replace(/\//g, "-")
    } else if (type === 7) {
      // 23-01-2024 21:25
      const option7 = `${formatDate(6)} ${datetime.toLocaleTimeString("en-GB", {
        hour: "2-digit",
        minute: "2-digit"
      })}`
      return option7
    } else if (type === 8) {
      // 2024-01-23
      return datetime.toISOString().split("T")[0]
    } else if (type === 9) {
      // 2024/01/23
      return datetime
        .toLocaleDateString("en-CA", {
          day: "2-digit",
          month: "2-digit",
          year: "numeric"
        })
        .replace(/\-/g, "/")
    } else {
      // Default format (type not recognized)
      return "Invalid type"
    }
  }

